define('ember-parallel-model-demo/routes/authenticated/parent/child', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    beforeModel: function beforeModel() {
      return new Ember['default'].RSVP.Promise(function (resolve, reject) {
        setTimeout(function () {
          console.log("beforeModel - child");resolve({ whichModel: "child" });
        }, 1500);
      });
    },
    model: function model() {
      var that = this;
      this.modelFor('authenticated');
      return new Ember['default'].RSVP.Promise(function (resolve, reject) {
        setTimeout(function () {
          console.log("model - child");
          resolve({ whichModel: "child" });
          setTimeout(function () {
            that.modelFor('authenticated');
          }, 2000);
        }, 1500);
      });
    },
    afterModel: function afterModel() {
      return new Ember['default'].RSVP.Promise(function (resolve, reject) {
        setTimeout(function () {
          console.log("afterModel - child");resolve({ whichModel: "child" });
        }, 2500);
      });
    }
  });

});