/* jshint ignore:start */

define('ember-parallel-model-demo/config/environment', ['ember'], function(Ember) {
  var prefix = 'ember-parallel-model-demo';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("ember-parallel-model-demo/tests/test-helper");
} else {
  require("ember-parallel-model-demo/app")["default"].create({"name":"ember-parallel-model-demo","version":"0.0.0+ca2a36cd"});
}

/* jshint ignore:end */
